function CuisineLinks(props) {
  function handleClick(event) {
    props.onClick(event.target.textContent);
  }
  return (
    <div>
      <button className="btn btn-greek" onClick={handleClick}>
        Greek
      </button>
      <button className="btn btn-italian" onClick={handleClick}>
        Italian
      </button>
      <button className="btn btn-chinese" onClick={handleClick}>
        Chinese
      </button>
      <button className="btn btn-mexican" onClick={handleClick}>
        Mexican
      </button>
      <button className="btn btn-indian" onClick={handleClick}>
        Indian
      </button>
      <button className="btn btn-french" onClick={handleClick}>
        French
      </button>
    </div>
  );
}
export default CuisineLinks;
