import Grid from "@mui/material/Grid";
import "./style.css";

function Recipe(props) {
  const { recipe, ingredients } = props;

  let instructions = recipe.strInstructions.split("\n");

  // remove blank lines or entries of length of 1
  instructions = instructions.filter(
    (item) => item.length > 2 || item.toLowerCase().substring(0, 4) === "step"
  );

  instructions = instructions.map((item, index) => (
    <p key={index}>
      <span>
        <b>{`Step ${index + 1}: `}</b>
      </span>
      {item}
    </p>
  ));

  return (
    <Grid container spacing={2}>
      <Grid
        item
        xs={4}
        style={{
          backgroundImage: `url(${recipe.strMealThumb})`,
          marginTop: 20,
        }}
      ></Grid>

      <Grid item xs={8}>
        <span className="recipe-heading">
          <h2>{recipe.strMeal}</h2> | <h4>{recipe.strCategory}</h4> |{" "}
          <h4>{recipe.strArea}</h4> |{" "}
          <a href={recipe.strYoutube} target="_blank">
            <img
              src="https://img.icons8.com/color/48/000000/youtube-play.png"
              width="40px"
            />
          </a>
        </span>
        <Grid container spacing={2}>
          <Grid item xs={8} className="left-align">
            {instructions}
          </Grid>
          <Grid item xs={4} className="left-align">
            <ul>
              {ingredients.map((ingredient, index) => (
                <li key={index}>{ingredient}</li>
              ))}
            </ul>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
}
export default Recipe;
