import { useEffect, useState } from "react";
import "./style.css";
import Input from "../Input";
import CuisineLinks from "../CuisineLinks";
import Recipe from "../Recipe";
import DOMPurify from "dompurify";

function makeIngredientsArray(data) {
  const ingredientsArray = [];
  for (let i = 1; i <= 20; i++) {
    if (data.meals[0][`strIngredient${i}`]) {
      ingredientsArray.push(
        `${data.meals[0][`strIngredient${i}`]} - ${
          data.meals[0][`strMeasure${i}`]
        }`
      );
    } else {
      break;
    }
  }
  return ingredientsArray;
}

function App() {
  const [recipe, setRecipe] = useState(null);
  const [error, setError] = useState(null);
  const [ingredients, setIngredients] = useState([]);

  useEffect(() => {
    async function getRecipe() {
      const response = await fetch(
        `https://www.themealdb.com/api/json/v1/1/random.php`
      );
      const data = await response.json();
      setRecipe(data.meals[0]);

      setIngredients(makeIngredientsArray(data));
    }
    getRecipe();
  }, []);

  async function searchRecipe(query) {
    try {
      const cleanQuery = DOMPurify.sanitize(query, { ALLOWED_TAGS: [] });
      const response = await fetch(
        `https://www.themealdb.com/api/json/v1/1/search.php?s=${cleanQuery}`
      );
      const data = await response.json();
      if (data.meals === null) {
        setError("No recipes found. Please try another search.");
        setRecipe(null);
      } else {
        setError(null);
        setRecipe(data.meals[0]);

        setIngredients(makeIngredientsArray(data));
      }
    } catch (err) {
      console.error(err);
      setError(
        "An error occurred while fetching the recipe. Please try again."
      );
      setRecipe(null);
    }
  }

  async function getNationality(query) {
    const cleanQuery = DOMPurify.sanitize(query, { ALLOWED_TAGS: [] });
    const response = await fetch(
      `https://www.themealdb.com/api/json/v1/1/filter.php?a=${cleanQuery}`
    );
    const data = await response.json();

    const randomRecipe =
      data.meals[Math.floor(Math.random() * data.meals.length)].idMeal;

    //www.themealdb.com/api/json/v1/1/lookup.php?i=52772
    const response2 = await fetch(
      `https://www.themealdb.com/api/json/v1/1/lookup.php?i=${randomRecipe}`
    );
    const data2 = await response2.json();
    setRecipe(data2.meals[0]);
    setIngredients(makeIngredientsArray(data2));
  }

  return (
    <div className="App">
      <Input onSubmit={searchRecipe} />
      <CuisineLinks onClick={getNationality} />
      {error && <div className="error">{error}</div>}
      {recipe && <Recipe recipe={recipe} ingredients={ingredients} />}
    </div>
  );
}

export default App;
